import React from "react";
import Axios from "axios";
import Moive from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: [],
  };
  getMovies = async () => {
    // async는 이 함수가 '비동기'라고 한거다!  await는 함수 내부에서 '기다려야 하는 부분'을 표시한다! (이 둘은 짝으로 사용된다!)
    const {
      data: {
        data: { movies },
      },
    } = await Axios.get(
      "https://yts-proxy.now.sh/list_movies.json?sort_by=rating"
    ); // await는 이 함수가 '비동기' 라고 한거다! 이 말은 너는 이걸 기다려야해!! 라는 의미이다.
    this.setState({ movies, isLoading: false });
  };

  async componentDidMount() {
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state; //ES6 문법이다 !
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map((movie) => (
              <Moive
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default App;
